/* '#a1c3d1',  '2B4141', '861657', 'F7B2B7', '7F2982', '7F7EFF' */
.menu-professional-container {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 15px;
  right: 17px;
  transition: top 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9); }

nav.menu-professional {
  display: block;
  width: 800px;
  height: 800px;
  -webkit-transform: translate3d(12.5px), -12.5px, 0;
  transform: translate3d(12.5px), -12.5px, 0;
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9);
  transition: -webkit-transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9);
  transition: transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9), -webkit-transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9); }

nav.menu-professional {
  position: relative;
  bottom: 350px;
  right: 350px;
  visibility: hidden;
  transition: visibility 0s step-end 0.7s; }

.open-professional.menu-professional {
  visibility: visible;
  transition: visibility 0s step-start 0s; }

a {
  color: #f0ebf4;
  text-decoration: none; }

.disc-professional {
  position: absolute;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  font-size: 25px;
  font-family: inherit;
  line-height: 40px;
  border-radius: 400px;
  -webkit-transform: scale3d(0.5, 0.5, 0.5) rotate3d(0, 0, 1, 190deg);
  transform: scale3d(0.5, 0.5, 0.5) rotate3d(0, 0, 1, 190deg);
  pointer-events: none;
  opacity: 0;
  cursor: pointer;
  transition: opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9);
  transition: transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9), opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.3, 1.4, 0.5, 0.9); }

.disc-professional div {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg); }
  .disc-professional div img {
    width: 100%; }

.open-professional .disc-professional {
  pointer-events: auto;
  opacity: 1; }

.disc-professional:hover a {
  color: inherit; }

