/* ********************* */
.toggle-professional {
  line-height: 100px;
  padding: 0;
  width: 100px;
  background: #e84398;
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  pointer-events: auto;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  color: #921620; }

.open-professional .toggle-professional {
  -webkit-transform: rotate3d(0, 0, 1, 0deg);
  transform: rotate3d(0, 0, 1, 0deg); }

.toggle-professional:hover {
  background: #7f7eff;
  color: #7f7eff; }
  .toggle-professional:hover svg {
    fill: #7f7eff; }

.toggle-professional:active {
  background: #f7b2b7;
  color: #440c47;
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  -webkit-transition-duration: 0s;
  transition-duration: 0s; }
