/* Level 1 */
.l1 {
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: #7f2982;
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

.open .l1 {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 190deg);
  transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 190deg);
  opacity: 1; }

.open .l1:hover {
  background: #7f7eff;
  color: #440c47;
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

.open .l1:active {
  background: #f7b2b7;
  color: #440c47; }

.open .l1.toggle {
  -webkit-transform: rotate3d(0, 0, 1, 10deg);
  transform: rotate3d(0, 0, 1, 10deg); }

